// Screen for viewing changelog and credits.
// Contained in drawer navigator to separate from other screens.
function AboutScreen() {
  // Access theme colors.
  const { colors } = useTheme();

  return (
    <ScrollView
      contentContainerStyle={{
        alignItems: 'center',
      }}>
      <Text style={[styles.creditText, {color: colors.text}]}>
        App created by
      </Text>
      <Text style={[
        styles.creditText, styles.locCount, {color: colors.text}
      ]}>
        Kevin Kolpack (Grandmauden)
      </Text>

      <View style={[styles.credit, styles.creditCenter]}>
        <Text style={[styles.creditText, {color: colors.text}]}>
          This app is not affiliated with and is not supported by
        </Text>
        <Text style={[styles.creditText, {color: colors.text}]}>
          Looking Glass Studios, Ion Storm - Austin, Eidos Montral,
        </Text>
        <Text style={[styles.creditText, {color: colors.text}]}>
          Eidos Interactive, Square Enix, or Embracer Group.
        </Text>
      </View>

      {/* API section */}
      <Lists.SectionHeader headerName="APIs Used"/>
      <View style={styles.credit}>
        {/* Map out each entry in API array. */}
        {credits.api.map((apiKey, apiIndex) => (
          <View
            key={`api_${apiIndex}`}
            style={styles.creditCenter}
          >
            <Text style={[
              styles.creditText, styles.locCount, {color: colors.text}
            ]}>
              {`${apiKey.name}:`}
            </Text>
            <Text
              style={[styles.url, {color: colors.url}]}
              onPress={() => Linking.openURL(apiKey.link)}
            >
              {`${apiKey.link}`}
            </Text>
          </View>
        ))}
      </View>

      {/* Special Thanks section */}
      <Lists.SectionHeader headerName="Special Thanks"/>
      <View style={[styles.credit, styles.creditCenter]}>
        {/* Map out each entry in special thanks array. */}
        {credits.thanks.map((thankKey, thankIndex) => (
          <View key={`thank_${thankIndex}`}>
            <Text style={[styles.creditText, {color: colors.text}]}>
              <Text style={styles.locCount}>
                {`${thankKey.name}: `}
              </Text>
              {`${thankKey.reason}`}
            </Text>
          </View>
        ))}
      </View>

      {/* Changelog section */}
      <Lists.SectionHeader headerName="Changelog"/>
      <View>
        {/* Map out each entry in changelog array. */}
        {credits.changelog.map((logKey, logIndex) => (
          <View key={`log_${logIndex}`}>
            <View style={[styles.changelog, styles.creditCenter]}>
              <Text style={[
                styles.creditText, styles.locCount, {color: colors.text}
              ]}>
                {`${logKey.version}`}
              </Text>
              <Text style={[styles.creditText, {color: colors.text}]}>
                {`${logKey.date}`}
              </Text>
            </View>
            <View>
              {logKey.changes.map((changeKey, changeIndex) => (
                <View key={`change_${changeIndex}`}>
                  <Text style={[styles.creditText, {color: colors.text}]}>
                    {` ${changeKey}`}
                  </Text>
                </View>
              ))}
            </View>
            <View style={styles.creditCenter}>
              <Text style={[styles.changeLine, {color: colors.text}]}>
                {(Platform.OS === 'web')
                  ? '                                                  '
                  : '                                        '
                }
              </Text>
            </View>
          </View>
        ))}
      </View>
    </ScrollView>
  );
}


