// Screen with nested drawer navigator.
// Contained in stack navigator to allow going back in stack.
// Drawer won't appear in other stack screens.
// Default screen when app is opened for the first time.
function StartScreen() {
  return (
    <Drawer.Navigator>
      <Drawer.Screen
        name="Lootlists"
        component={MissionScreen}
        //options={{headerShown: false}}
      />
      {/*<Drawer.Screen
        name="City Map"
        component={MapScreen}
        //options={{headerShown: false}}
      />*/}
      <Drawer.Screen
        name="Settings"
        component={SettingsScreen}
        //options={{headerShown: false}}
      />
      <Drawer.Screen
        name="About"
        component={AboutScreen}
        options={{title: "About This App"}}
      />
    </Drawer.Navigator>
  );
}


